<template>
    <div>
        <Head/>
        <!-- 笔记 -->
        <div style="padding-top: 85px">
            <el-container>
                <!-- 左侧边栏 -->
                <el-aside width="250px" style="background-color: rgb(84, 92, 100); text-align: left;">
                    <!-- 菜单 -->
                    <el-menu
                        background-color="#545C64"
                        text-color="#fff"
                        active-text-color="#ffd04b"
                        @select="handleSelect"
                    >
                        <!-- 子菜单 -->
                        <el-submenu v-for="(menuData, index) in menuDataList" :key="index+''" :index="index+''">
                            <template slot="title"><i :class=menuData.icon></i><span>{{menuData.title}}</span></template>
                            <el-menu-item-group>
                                <el-menu-item :index=menuData.index>{{menuData.itemName}}</el-menu-item>
                            </el-menu-item-group>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <!-- 右侧容器 -->
                <el-container>
                    <el-main style="border-top:1px solid #E6E6FA">
                        <!-- README.md文件 -->
                        <div class="markdown-body" style="min-height: 800px;text-align: left;">
                            <vue-markdown v-html="readmeHtml"></vue-markdown>
                            <!-- <mavon-editor
                                v-model="content"
                                ref="md"
                                @change="change"
                            /> -->
                        </div>
                    </el-main>
                </el-container>
            </el-container>
        </div>
    </div>
</template>

<style>
    .el-header {
        background-color: #B3C0D1;
        color: #333;
        line-height: 60px;
    }

    .el-aside {
        color: #333;
    }

    .el-menu {
        border-right: solid 0px #e6e6e6;
        border-right-width: 0px;
        border-right-style: solid;
        border-right-color: rgb(230, 230, 230);
        list-style: none;
        position: relative;
        margin: 0;
        padding-left: 0;
        background-color: #FFF;
    }
</style>

<script>
import Head from '@/components/Header'
import VueMarkdown from 'vue-markdown';
import SpringBoogReadme from '../assets/md/Spring Boot.md'
// 导入组件 及 组件样式
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import 'github-markdown-css/github-markdown.css'
  export default {
    components: {
        Head, VueMarkdown, mavonEditor
    },
    data() {
      return {
        menuDataList: [
            {
                index: '1-1',
                icon: 'el-icon-document',
                title: 'Spring Boot',
                itemName: 'Spring Boot笔记',
                readme: SpringBoogReadme
            }
        ],
        springBoogReadme: SpringBoogReadme,
        content: SpringBoogReadme, // 输入的markdown
        html: '', // 及时转的html
        readmeHtml: SpringBoogReadme
      }
    },
    methods: {
        handleSelect (key, keyPath) {
            // 根绝key获取 itemName 即为readme文件
            const readme = this.getReadmeNameByIndex(key);
            // 根据readme文件 渲染文件
            this.renderView(readme);
        },
        getReadmeNameByIndex (index) {
            let readme;
            for (const value of this.menuDataList) {
                if (index === value.index) {
                    readme = value.readme;
                    break;
                }
            }
            return readme;
        },
        renderView (readme) {
            this.readmeHtml = readme;
        },
        // 所有操作都会被解析重新渲染
        change(value, render) {
            // render 为 markdown 解析后的结果[html]
            this.html = render;
        }
    }
  };
</script>


